'use client'
import { useEffect, useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "../ui/card";
import { Field, FieldGroup, FieldLabel } from "../ui/field";
import { Input } from "../ui/input";
import { Select, SelectContent, SelectGroup, SelectItem, SelectLabel, SelectTrigger, SelectValue } from "../ui/select";
import { CategoryType } from "../Admin/Category/CategoryList";
import { ChevronDownIcon } from "lucide-react"
import ImageUploader from "@/components/ImageUploader"
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover"
import { Calendar } from "@/components/ui/calendar"
import { Button } from "../ui/button";
import { Textarea } from "../ui/textarea";

export default function CourseForm() {
  const [categories, setCategories] = useState<CategoryType[]>([]);
  const [type, setType] = useState("")
  const [open, setOpen] = useState(false)
  const [date, setDate] = useState<Date | undefined>(undefined)

  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const res = await fetch(`${process.env.NEXT_PUBLIC_BASE_URL}/api/vendor/categories`);

        if (res.ok) {
          const data = await res.json();

          setCategories(data.data)
        }

      } catch (error) {
        console.error("Failed to fetch categories:", error);
      }
    };

    fetchCategories();
  }, []);

  return (
    <div className="grid grid-cols-12 gap-4">
      <Card className="col-span-12 lg:col-span-7 md:col-span-12">
        <CardHeader>
          <CardTitle>Add New Course</CardTitle>
          <CardDescription>Enter your course information below to add your course</CardDescription>
        </CardHeader>
        <CardContent>
          <FieldGroup>
            <div className="grid grid-cols-1 md:grid-cols-1 lg:grid-cols-2 gap-4">
              <Field>
                <FieldLabel htmlFor="name">Title</FieldLabel>
                <Input id="name" type="text" placeholder="course title" required />
              </Field>
              <Field>
                <FieldLabel htmlFor="category">Category</FieldLabel>
                <Select>
                  <SelectTrigger>
                    <SelectValue placeholder="Select a Category" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectGroup>
                      <SelectLabel>Categories</SelectLabel>
                      {Array.isArray(categories) && categories.map((category) => (
                        <SelectItem key={category.id} value={category.name}>
                          {category.name}
                        </SelectItem>
                      ))}
                    </SelectGroup>
                  </SelectContent>
                </Select>
              </Field>
              <Field>
                <FieldLabel htmlFor="price">Price</FieldLabel>
                <Input id="price" name="price" type="number" placeholder="course price" required step={0.01} />
              </Field>
              <Field>
                <FieldLabel htmlFor="discount">Discount<sub>(Optional)</sub></FieldLabel>
                <Input id="discount" type="number" placeholder="course discount" required step={0.01} />
              </Field>
              <Field>
                <FieldLabel htmlFor="course_type">Course Type</FieldLabel>
                <Select onValueChange={setType}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select a type" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectGroup>
                      <SelectLabel>Types</SelectLabel>
                      <SelectItem value="recorded">Recorded</SelectItem>
                      <SelectItem value="live">Live</SelectItem>
                    </SelectGroup>
                  </SelectContent>
                </Select>
              </Field>
              {type === 'live' && (
                <>
                  <Field>
                    <FieldLabel htmlFor="enrollment_close_date" className="px-1">
                      Enrollment Last Date
                    </FieldLabel>
                    <Popover open={open} onOpenChange={setOpen}>
                      <PopoverTrigger asChild>
                        <Button
                          variant="outline"
                          id="date"
                          className="w-48 justify-between font-normal"
                        >
                          {date ? date.toLocaleDateString() : "Select date"}
                          <ChevronDownIcon />
                        </Button>
                      </PopoverTrigger>
                      <PopoverContent className="w-auto overflow-hidden p-0" align="start">
                        <Calendar
                          mode="single"
                          selected={date}
                          captionLayout="dropdown"
                          onSelect={(date) => {
                            setDate(date)
                            setOpen(false)
                          }}
                        />
                      </PopoverContent>
                    </Popover>
                  </Field>
                  <Field>
                    <FieldLabel htmlFor="streaming_type">Streaming Type</FieldLabel>
                    <Select onValueChange={setType}>
                      <SelectTrigger>
                        <SelectValue placeholder="Select a type" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectGroup>
                          <SelectLabel>Types</SelectLabel>
                          <SelectItem value="external">External</SelectItem>
                          <SelectItem value="internal">Internal</SelectItem>
                        </SelectGroup>
                      </SelectContent>
                    </Select>
                  </Field>
                </>
              )}
              
            </div>
            <div className="grid grid-cols-1">
              <Field>
                <FieldLabel htmlFor="description">Description</FieldLabel>
                <Textarea placeholder="enter your course description"></Textarea>
              </Field>
            </div>
          </FieldGroup>
        </CardContent>
      </Card>
    </div>
  );
}
